.App {
  text-align: center;
}

.no-x-bar {
  overflow-x: hidden; //horizontal
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.section {
  height: 100vh;
}

.floating-icon {
  position: absolute;
  bottom:5px;
}
